.Product{
  width: 200px;
  height: 250px;
  background-color: transparent;
  box-shadow:  0px 0px 10px #dfdcdc;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.Product .imgcont{
  width: 100%;
  height: 50%;
}
.imgcont img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}
h1 ,h3{
  height: 2%;
  width: 100%;
}
.Products{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 500px;
  position: relative;
}
.cart{
  width: 10%;
  min-height: 90vh;
  height: fit-content;
  position: absolute;
  right: 5px;
  background-color: red;

}
.cart img{
  width: 100px;
  height: 100px;
}
.cart h1{
  width: 10px;
  height: 10px;
}
.cart h3{
  width: 10px;
  height: 10px;
}
.cart h4{
font-size: 40px;
}